import React, { useState } from 'react'
import { graphql } from 'gatsby'
import PropTypes from 'prop-types'

import { faHome, faListAlt } from '@fortawesome/free-solid-svg-icons'

import AppContext from '@src-contexts/app-context'
import SEO from '@src-components/common/seo'

import Layout from '@src-components/layout'

import PageHeading from '@src-components/parts/page-heading'
import ArticleList, {
	ArticleListItem,
} from '@src-components/parts/article-list'
import Pager from '@src-components/parts/pager'

const PostsPaginationTemplate = props => {
	const { location, data, pageContext } = props
	const {
		currentPage,
		canonical,
		totalPage,
		hasNextPage,
		hasPrevPage,
		prevPagePath,
		nextPagePath,
	} = pageContext

	const [isOpenMenu, setIsOpenMenu] = useState(false)
	const [isOpenSearch, setIsOpenSearch] = useState(false)

	const pageTitle = '記事一覧'
	const pageDescription = ''

	const pageValues = {
		title: pageTitle,
		description: pageDescription,
		location: location.pathname,
		canonical: canonical,
		breadcrumbItems: [
			{ label: 'ホーム', url: '/', icon: faHome },
			{ label: pageTitle, url: location.pathname, icon: faListAlt },
		],
		isOpenMenu: isOpenMenu,
		setIsOpenMenu: setIsOpenMenu,
		isOpenSearch: isOpenSearch,
		setIsOpenSearch: setIsOpenSearch,
	}

	return (
		<AppContext.Provider value={pageValues}>
			<SEO />
			<Layout pattern="one-column">
				<PageHeading icon={faListAlt}>{pageTitle}</PageHeading>
				<section className="mt-8">
					<ArticleList>
						{data.allMdx.edges.map(edge => {
							return <ArticleListItem key={edge.node.id} item={edge} />
						})}
					</ArticleList>
					<Pager
						current={currentPage}
						max={totalPage}
						parentPath={`/posts/`}
						prevPath={prevPagePath}
						nextPath={nextPagePath}
						hasPrev={hasPrevPage}
						hasNext={hasNextPage}
					/>
				</section>
			</Layout>
		</AppContext.Provider>
	)
}

PostsPaginationTemplate.propTypes = {
	pageContext: PropTypes.object,
}

export default PostsPaginationTemplate

export const pageQuery = graphql`
	query($limit: Int!, $offset: Int!) {
		allMdx(
			limit: $limit
			skip: $offset
			sort: { fields: [frontmatter___modifiedTime], order: DESC }
			filter: { frontmatter: { type: { eq: "post" }, draft: { ne: true } } }
		) {
			edges {
				node {
					id
					frontmatter {
						title
						description
						thumbnail
						categories
						tags
						publishTime
						modifiedTime
					}
					fields {
						slug
						name
					}
				}
			}
		}
	}
`
